import { Component, EventEmitter, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-start-menu-layer',
  templateUrl: './start-menu-layer.component.html',
  styleUrls: ['./start-menu-layer.component.scss']
})
export class StartMenuLayerComponent implements OnInit {
  @Output() onClose = new EventEmitter<any>();

  constructor() {
  }

  ngOnInit(): void {
  }

  manageClick(ev: MouseEvent) {
    ev.preventDefault();
    // ev.stopPropagation();
    this.onClose.emit();
  }

  manageContextMenu(ev: MouseEvent) {
    ev.preventDefault();
    // ev.stopPropagation();
    this.onClose.emit();
  }
}
