import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { BootService } from '../services/os/boot.service';

@Injectable({
  providedIn: 'root'
})
export class BootGuard implements CanActivate {
  constructor(private bootSv: BootService, private router: Router) {
  }

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    if (!this.bootSv.hasBooted) {
      this.router.navigate(["boot"]);

      return false;
    }

    return true;
  }
}
